import { useEffect } from 'react';
import './App.css';
import Protected from './features/auth/Components/Protected';
import CartPage from './Pages/CartPage';
import Checkout from './Pages/Checkout';
import Home from './Pages/Home';
import LoginPage from './Pages/LoginPage';
import ProductDetailPage from './Pages/ProductDetailPage';
import SignupPage from './Pages/SignupPage';

import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';
import { fetchItemsByUserIdAsync } from './features/Cart/cartSlice';
import { checkAuthAsync, selectLoggedInUser, selectUserChecked } from './features/auth/authSlice';
import PageNotFound from './Pages/404';
import OrderSuccessPage from './Pages/OrderSuccessPage';
import UserOrderPage from './Pages/UserOrderPage';
import UserProfilePage from './Pages/UserProfilePage';
import { fetchLoggedInUserAsync } from './features/user/userSlice';
import Logout from './features/auth/Components/Logout';
import ForgotPasswordPage from './Pages/ForgotPasswordPage';
import AdminHome from './Pages/AdminHome';
import ProtectedAdmin from './features/auth/Components/ProtectedAdmin';
import AdminProductDetailPage from './Pages/AdminProductDetailPage';
import AdminProductFromPage from './Pages/AdminProductFormPage';
import AdminOrderPage from './Pages/AdminOrderPage';
import StripeCheckout from './Pages/StripeCheckout'
import { Bounce, ToastContainer } from 'react-toastify';

const router = createBrowserRouter([
  {
    path: "/",
    element: <Protected>
      <Home></Home>
    </Protected>
  },
  {
    path: "/admin",
    element: <ProtectedAdmin><AdminHome></AdminHome></ProtectedAdmin>
  },
  {
    path: "/login",
    element: <LoginPage></LoginPage>,
  },
  {
    path: "/signup",
    element: <SignupPage></SignupPage>,
  },
  {
    path: "/cart",
    element: <Protected><CartPage></CartPage></Protected>,
  },
  {
    path: "/checkout",
    element: <Protected><Checkout></Checkout>,</Protected>
  },
  {
    path: "/product-detail/:id",
    element: <Protected><ProductDetailPage></ProductDetailPage></Protected>,
  },
  {
    path: "/admin/product-detail/:id",
    element: <ProtectedAdmin><AdminProductDetailPage></AdminProductDetailPage></ProtectedAdmin>,
  },
  {
    path: "/admin/product-form",
    element: <ProtectedAdmin><AdminProductFromPage></AdminProductFromPage></ProtectedAdmin>,
  },
  {
    path: "/admin/orders",
    element: <ProtectedAdmin><AdminOrderPage></AdminOrderPage></ProtectedAdmin>,
  },
  {
    path: "/admin/product-form/edit/:id",
    element: <ProtectedAdmin><AdminProductFromPage></AdminProductFromPage></ProtectedAdmin>,
  },
  {
    path: "/order-success/:id",
    element: <OrderSuccessPage></OrderSuccessPage>,
  },
  {
    path: "/orders",
    element: (<Protected><UserOrderPage></UserOrderPage>{' '}</Protected>),
  },
  {
    path: "/profile",
    element: (<Protected><UserProfilePage></UserProfilePage>{' '}</Protected>),
  },
  {
    path: "/stripe-checkout/",
    element: (<Protected><StripeCheckout></StripeCheckout></Protected>),
  },
  {
    path: "/logout",
    element: <Logout></Logout>,
  },
  {
    path: "/forgot-password",
    element: <ForgotPasswordPage></ForgotPasswordPage>,
  },
  {
    path: "*",
    element: <PageNotFound></PageNotFound>,
  },
]);


function App() {
  const dispatch = useDispatch()
  const user = useSelector(selectLoggedInUser)
  const userChecked = useSelector(selectUserChecked)

  useEffect(() => {
    dispatch(checkAuthAsync())
  }, [dispatch])


  useEffect(() => {
    if (user) {
      dispatch(fetchItemsByUserIdAsync())
      // we can get req.user by token on backend so no need to given in front-end
      dispatch(fetchLoggedInUserAsync())
    }
  }, [dispatch, user])

  return (
    <div className="App">
      <RouterProvider router={router} />
      {/* Link must be inside the provider */}
      {userChecked && < ToastContainer
        position="top-center"
        autoClose={1000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick={false}
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
        transition={Bounce}
      />}
    </div >
  );
}

export default App;
